// Component Variables


// Rules
.background {
    z-index: -1;
    display: block;
    position: relative;
}

.background__gradient {
    @media not print {
        position: fixed;
        top: 0;
        bottom: 0;
        right: 0;
        left: 0;
        background: radial-gradient($brand1-color, $brand2-color);
        z-index: 0;
        opacity: 0.1;
        pointer-events: none;
    }
}

.background__canvas {
    display: none;
    
    @media not print {
        display: block;
        position: fixed;
        top: 0;
        left: 0;
        z-index: -1;
    }
}


