body{
    box-sizing: border-box;
}

//Sizes
.section{
    box-sizing: border-box;
    padding: $small-space $xsmall-space;

    @media (min-width: $small-mobile) {
        padding: $medium-space $xsmall-space;
    }
    @media (min-width: $small-tablet) {
        padding: $medium-space $small-space;
    }
    @media (min-width: $small-desktop) {
        padding: $large-space $medium-space;
    }
    
    @media print {
        padding-top: $xsmall-space;
        padding-bottom: $xsmall-space;
    }
    
}

.container{
    max-width: $container-max-width;
    margin: 0 auto;
}
